import { compare } from "compare-versions"
import React, {
  useMemo,
  useCallback,
  useEffect,
  useState,
  useReducer,
} from "react"
import { useMediaQuery } from "react-responsive"
import styled from "styled-components/macro"


import { skipTr } from "lib/lingui"

import gameRestartImg from "../../../assets/img/icon/gameRestart.png"
import gamesLightImg from "../../../assets/img/icon/gamesLight.png"
import pauseLightImg from "../../../assets/img/icon/pauseLight.png"
import settingsLightImg from "../../../assets/img/icon/settingsLight.png"

import { DevSettings, useDevSettings } from "./components/Settings/DevSettings"
import { GameErrorModal } from "./components/Settings/GameErrorModal"
import { flex } from "./lib/flex"
import { useCLIData } from "./lib/useCLIData"
import { webTheme } from "./styles/webTheme"
import { BottomBarAction } from "./views/GameView/BottomBar"
import { GameView } from "./views/GameView/GameView"

const minSdkVersion = "2.0.2"

export function AppCLI() {
  const { devSettings, setDevSettings } = useDevSettings()
  const isDesktop = useMediaQuery({ query: "(min-width: 992px)" })
  const { data } = useCLIData()

  const [errMsg, setErrorMessage] = useState<string | null>(null)
  const [gameViewId, rerenderGameView] = useReducer((state) => state + 1, 0)

  useEffect(() => {
    if (!data) return

    const availableGameUrl =
      location.hostname === "localhost"
        ? data.gameUrl
        : data.gameUrl.replace("localhost", location.hostname)

    const gameUrlWithSlashAtEnd = availableGameUrl.endsWith("/")
      ? availableGameUrl
      : `${availableGameUrl}/`

    setDevSettings({
      cliVersion: data.cliVersion,
      game: {
        id: 1,
        challengeId: 1,
        nextChallengeAt: null,
        url: `${gameUrlWithSlashAtEnd}?enableInitialOverlayInBrowser=1`,
        blurredImg:
          "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mNs+A8AAgUBgQvw1B0AAAAASUVORK5CYII=",
        logoUrl: gamesLightImg,
        title: "Game Title",
        playCount: 99000,
        commentCount: 99000,
      },
    })
  }, [data, setDevSettings])

  const openSettings = useCallback(() => setDevSettings({ visible: true }), [
    setDevSettings,
  ])

  const bottomBarActions = useMemo<{
    playing: BottomBarAction[]
    paused: BottomBarAction[]
  }>(
    () => ({
      playing: [
        { imgSrc: gameRestartImg, onClick: ({ onRestart }) => onRestart() },
        { imgSrc: pauseLightImg, onClick: ({ onPause }) => onPause() },
        { imgSrc: settingsLightImg, onClick: openSettings },
      ],
      paused: [
        {
          imgSrc: gameRestartImg,
          onClick: ({ onRestart }) => onRestart(),
          disabled: true,
        },
        { spacer: true },
        { imgSrc: settingsLightImg, onClick: openSettings },
      ],
    }),
    [openSettings]
  )

  const setSdkVersion = useCallback(
    (sdkVersion: string) => setDevSettings({ sdkVersion }),
    [setDevSettings]
  )

  const sdkVersionSupported = useMemo(
    () =>
      !!devSettings.sdkVersion &&
      compare(devSettings.sdkVersion, minSdkVersion, ">="),
    [devSettings.sdkVersion]
  )

  const setScore = useCallback((score: number) => setDevSettings({ score }), [
    setDevSettings,
  ])

  const onGameError = useCallback(
    (errMsg: string) => setErrorMessage(errMsg),
    []
  )

  const onCloseErrorModal = useCallback(() => {
    setErrorMessage(null)
    rerenderGameView()
  }, [])

  if (!devSettings.game) return null

  if (devSettings.sdkVersion && !sdkVersionSupported) {
    return (
      <Root>
        <Disclaimer>
          {skipTr(`Your game uses Rune SDK version ${devSettings.sdkVersion}.`)}
          <br />
          {skipTr(
            `This CLI tool only supports versions ${minSdkVersion} or higher.`
          )}
          <br />
          {skipTr(`Please update the SDK version in your index.html.`)}
        </Disclaimer>
      </Root>
    )
  }

  const gameView = (
    <GameView
      key={gameViewId}
      game={devSettings.game}
      isPortrait
      shouldRenderIframe
      bottomBarActions={bottomBarActions}
      onSdkVersionReported={setSdkVersion}
      scoreRequestTrigger={devSettings.scoreRequestTrigger}
      onScore={setScore}
      onGameError={onGameError}
      //Completely ignore high score logic
      currentScore={Infinity}
    />
  )

  return (
    <>
      <Root>
        {isDesktop && (
          <Disclaimer>
            {skipTr(
              `It looks like you're using a desktop device. The game might not be displaying correctly. Please use a mobile device simulator in Chrome, an iOS or Android simulator, or a physical device.`
            )}
          </Disclaimer>
        )}
        {isDesktop ? <PhoneContainer>{gameView}</PhoneContainer> : gameView}
      </Root>
      <DevSettings />
      <GameErrorModal
        visible={!!errMsg}
        message={errMsg}
        onClose={onCloseErrorModal}
      />
    </>
  )
}

const Root = styled.div`
  width: 100%;
  height: 100%;
  ${flex({ vAlign: "center", hAlign: "center" })};
  background-color: grey;
  > :not(:first-child) {
    margin-top: 20px;
  }
`

const Disclaimer = styled.div`
  ${webTheme.text.body};
  color: white;
  max-width: 500px;
  text-align: center;
`

const PhoneContainer = styled.div`
  width: 375px;
  height: 667px;
  border: 10px solid black;
  border-radius: 20px;
  box-sizing: content-box;
  overflow: hidden;
`
