import React from "react"
import styled from "styled-components/macro"

import { skipTr } from "lib/lingui"

import { flex } from "../../lib/flex"
import { webTheme } from "../../styles/webTheme"

import { Root, Modal, Text } from "./Modal"

export function GameErrorModal({
  visible,
  onClose,
  message,
}: {
  visible: boolean
  onClose: () => void
  message: string | null
}) {
  if (!visible) return null

  return (
    <Root onClick={onClose}>
      <Modal onClick={(e) => e.stopPropagation()}>
        <Line>
          <Text>{skipTr(`${message}`)}</Text>
          <Button onClick={onClose}>{skipTr("Reload Game")}</Button>
        </Line>
      </Modal>
    </Root>
  )
}

export const Line = styled.div`
  ${flex({ vAlign: "center", hAlign: "center" })};
`

const Button = styled.div`
  ${webTheme.text.body};
  margin-top: 20px;
  color: white;
  padding: 10px;
  text-align: center;
  background-color: #444;
  border-radius: 5px;
  cursor: pointer;
  user-select: none;
`
