import { createDeterministicPRNG } from "lib/createDeterministicPRNG"

const minFakeCount = 1100
const maxFakeCount = 3300

export function fakeGamePlayCountIfLow({
  realCount,
  gameId,
}: {
  realCount: number
  gameId: number
}) {
  return realCount > maxFakeCount
    ? realCount
    : getRandomInt(gameId, minFakeCount, maxFakeCount)
}

function getRandomInt(seed: number, min: number, max: number) {
  return Math.floor(createDeterministicPRNG(seed)() * (max - min) + min)
}
